:root {
  --search-bg: #80639b;
  --search-button: #c58ade;
  --favorites-bg: #f7db70;
  --favorites-button: #feb25e;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  /* font-family: "Courier New", Courier, monospace; */
  font-family: 'Titillium Web', sans-serif;
}

html {
  font-size: 625%;
}

