.profile-picture {
  border-radius: 100%;
  height: 2em;
}

.filter {
  /* background-color: darkgray; */
  background-color: #80639b;
  background-color: var(--search-bg);
  text-align: end;
}

.tabs {
  background-color: #80639b;
  background-color: var(--search-bg);
  margin-left: 0.5em;
}

.tabs > button.active {
  background-color: #c58ade;
  background-color: var(--search-button);
  color: white;
}

.editor {
  overflow-y: auto;
}

.tab-content {
  overflow-y: scroll;
}

@media (max-width: 960px) {
  .tab-content {
    overflow-y: visible;
  }
}

.tab-content > div {
  /* background-color: darkgray; */
  background-color: #f7db70;
  background-color: var(--favorites-bg);
}

.tab-content.blue-bg > div {
  background-color: #80639b;
  background-color: var(--search-bg);
}

.tab-content > div > div {
  margin: 0.5em;
  background-color: white;
  border-radius: 0.7em;
  padding: 0.7em;
}

.tab-content > div > div.open {
  height: 200px;
}

.tab-content-options {
  /* background-color: darkgray; */
  background-color: #f7db70;
  background-color: var(--favorites-bg);
}

.tab-content-options.blue-bg {
  background-color: #80639b;
  background-color: var(--search-bg);
}

/* -------------------------------------------------------------------------------------- */

.other-button {
  border-radius: 100px;
  background-color: black;
  color: white;
}

button, input {
  font-size: 0.8em;
  font-weight: bold;

  padding-left: 0.5em;
  padding-right: 0.5em;
}

button {
  /* height: 100%; */
  height: 2em;

  margin-left: 0.1em;
  margin-right: 0.1em;

  border-top-left-radius: 1em;
  border-top-right-radius: 1em;
  /* border-top-left-radius: 100%;
  border-top-right-radius: 100%; */

  /* border: solid black 3px; */
  border: hidden;
}

input {
  height: 2.5em;
  border: hidden;
  border-radius: 0.7em;
  margin: 0.5em;
}

.input-filter {
  width: 20em;
  background-color: rgba(255, 255, 255, 0.5);
}

