/* generated with https://grid.layoutit.com/ */

.container {
    display: grid;
    /* grid-template-columns: 1fr minmax(auto, 1200px) 1fr; */
    grid-template-columns: 1fr minmax(auto, 100vw) 1fr;
    grid-template-rows: 100vh;
    grid-auto-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      ". subcontainer .";
  }
  
  .subcontainer {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "user-account"
      "editor";
    grid-area: subcontainer;
  }
  
  .user-account { grid-area: user-account; }
  
  .editor {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "filter"
      "result-tabs"
      "favorite-tabs"
      "tab-content-options"
      "tab-content";
    grid-area: editor;
  }
  
  .filter { grid-area: filter; }
  
  .result-tabs { grid-area: result-tabs; }
  
  .favorite-tabs { grid-area: favorite-tabs; }
  
  .tab-content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      ". . .";
    grid-area: tab-content;
  }
  
  .tab-content-options { grid-area: tab-content-options; }
/* generated with https://grid.layoutit.com/ */

@media (max-width: 960px) {
  .tab-content {
    grid-template-columns: 1fr;
    grid-template-areas:
      ".";
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

